@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alegreya&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Titillium+Web&display=swap);
.tech__wrapper {
  display: flex;
  margin-bottom: 10px;
  justify-content: space-between;
  padding: 2em;
  border-bottom: 2px solid black;
  margin-bottom: 3em;
  margin-top: 22%;
  width: 100%;
}

.tech__logos {
  flex-direction: column;
}

.tech__logos img {
  width: 60px;
}

.tech__text {
  color: #2368a2;
  font-weight: 650;
}

.Typescript {
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.JavaScript {
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}

.React {
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: 4s;
          animation-duration: 4s;
}

.Redux {
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
}

.Node {
  -webkit-animation-name: fadeInUp;
          animation-name: fadeInUp;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
}

@-webkit-keyframes fadeInUp {
  40% {
    opacity: 0;
  }

  from {
    opacity: 0;
    transform: translate3d(0, 10px, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes fadeInUp {
  40% {
    opacity: 0;
  }

  from {
    opacity: 0;
    transform: translate3d(0, 10px, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@-webkit-keyframes loadLazy {
  from {
  }

  to {
    opacity: 1;
  }
}

@keyframes loadLazy {
  from {
  }

  to {
    opacity: 1;
  }
}

.homepage__wrapper {
  display: flex;
  min-height: 100vh;
}

.homepage__wrapper {
  margin-top: 10%;
  justify-content: center;
}

.homepage__name {
  font-size: 5em;
}

.homepage__role {
  text-align: center;
  font-size: 25px;
}

.footer__author,
.homepage__role {
  color: #a9a9ac;
}

@media (max-width: 500px) {
  .homepage__name {
    font-size: 4em;
  }
  .homepage__name__wrapper {
    flex-direction: column;
  }

  .homepage__location {
    text-align: center;
  }

  .homepage__info__wrapper {
    margin-top: 0;
  }

  .homepage__location {
    text-align: center;
  }
}

.portfolio__experience {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 5em 5em 5em;
}

.portfolio__experience--category {
  text-align: center;
  color: #141414;
}

.portfolio__experience__card,
.portfolio__education {
  background-color: #ffffff;
  border: solid 2px #bdb7b7;
  padding: 1em;
  margin: 1em;
  text-align: center;
  min-width: 350px;
}

.portfolio__experience--title,
.portfolio__wrapper--title {
  font-weight: 650;
  text-align: center;
  margin-top: 2em;
}

.cv__wrapper {
  justify-content: space-between;
}

.portfolio__wrapper {
  display: flex;
  justify-content: center;
}

.portfolio__page {
  margin-bottom: 15%;
}

.portfolio__education {
  margin-top: 2em;
  background-color: #ffffff;
  border: solid 2px #bdb7b7;
  flex-basis: 50%;
}

.portfolio__education p {
  font-size: 17px;
}

.portfolio__experience--category__wrapper {
  display: flex;
  justify-content: space-between;
}

.iconify {
  width: 50px;
}

.portfolio__education img {
  display: flex;
  max-width: 100%;
}

@media (max-width: 500px) {
  .portfolio__education p {
    font-size: 12px;
  }

  .portfolio__experience {
    margin: 0;
  }

  .portfolio__experience--category {
    font-size: 20px;
  }

  .portfolio__experience--title,
  .portfolio__wrapper--title {
    font-size: 20px;
    margin: 15px 0 0 8px;
  }

  .portfolio__experience,
  .portfolio__wrapper {
    flex-direction: column;
  }

  .portfolio__education img {
    width: 300px;
  }
}

.cv__wrapper {
  display: flex;
  color: white;
  margin: 0 0 2em;
}

.cv__wrapper p {
  margin: 1em;
  color: #343a40;
}

.cv__date {
  font-weight: 650;
  color: #1a4971;
}

.cv__wrapper img {
  max-height: 70px;
  max-width: 70px;
}

.project__wrapper,
.project__card,
.button__wrapper {
  display: flex;
  justify-content: space-evenly;
}

.project__card {
  flex-basis: 50%;
}

.project__text {
  font-size: 13pt;
  text-align: center;
}

.project__topic {
  color: #CB6A2F;
}

.project__title {
  text-align: center;
  font-size: 1.4em;
  color: #141414;
  margin: 1em;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 800;
}

.project__wrapper {
  margin-top: 1%;
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 10em;
}

.project__image {
  object-fit: cover;
  height: 250px;
}

.link__button {
  margin-bottom: 1em;
}

.btn:hover {
  color: green;
}

@media (max-width: 500px) {
  .project__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .project__card {
    margin: 0;
  }

  .project__image {
    height: 300px;
  }

  .project__title {
    font-size: 20px;
  }
}

#root {
  overflow-x: hidden;
}

.app__wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.navbar__routes,
.footer__links {
  padding: 1em;
}

.navbar__routes {
  background-color: #fbfbfd;
  z-index: 999;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  border-bottom: 2px solid rgba(38, 38, 38, 0.1);
}

.navbar__routes ul,
.footer__links ul {
  display: flex;
  flex-wrap: wrap;

  justify-content: center;
  list-style-type: none;
  text-transform: uppercase;
  margin: 0;
}

.navbar__routes img {
  width: 25px;
}

.navbar__routes a,
.navbar__routes a:hover,
.footer__links a,
.footer__links a:hover,
.footer__links p {
  text-decoration: none;
  font-weight: 650;
  padding: 5em;
}
.navbar__routes a,
.footer__links a {
  color: #333333;
}

.navbar__routes a:hover,
.footer__links a:hover {
  color: #3183c8;
}

.footer__links {
  background-color: #f5f5f7;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

body {
  background-color: #fbfbfd;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

body::-webkit-scrollbar {
  width: 10px;
}

body::-webkit-scrollbar-track {
  background: rgb(228, 228, 228);
}

body::-webkit-scrollbar-thumb {
  background-image: linear-gradient(180deg, #d0368a 0%, #708ad4 99%);
  box-shadow: inset 2px 2px 5px 0 rgba(#fff, 0.5);
  border-radius: 100px;
}

@media (max-width: 500px) {
  .navbar__routes,
  .footer__links {
    font-size: 0.8em;
  }

  .homepage__development {
    text-align: center;
  }

  .navbar__routes ul,
  .footer__links ul {
    justify-content: space-between;
  }

  .navbar__routes a,
  .navbar__routes a:hover,
  .footer__links a,
  .footer__links a:hover {
    font-weight: 800;
    padding: 1em;
  }

  ul {
    padding: 0;
  }
}

@media (max-width: 330px) {
  .navbar__routes,
  .footer__links {
    font-size: 0.6em;
  }
}

